$gray: #aeaeae;
$grayLight: #babcbb;
$grayDark: #767676;
$red: #cc0017;
$redLight: #ee0017;
$hoverBtn: #f8f8f8;
@charset "UTF-8";
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  font-family: custom-regular;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
div{
  -webkit-tap-highlight-color: transparent;
}
h1, h2, h3 {
  font-family: custom-regular;
}

  a:-webkit-any-link:focus {
    outline-offset: 1px;
  }

section {
  position: relative;
}

img {
  width: 100%;
  -o-object-fit: cover;
  object-fit: cover;
  -o-object-position: center;
  object-position: center;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

@font-face {
  font-family: 'custom-regular';
  src: url("../fonts/Mark-Simonson/regular/ProximaNova-Regular.eot") format("eot"),
  url("../fonts/Mark-Simonson/regular/ProximaNova-Regular.woff") format("woff"),
  url("../fonts/Mark-Simonson/regular/ProximaNova-Regular.ttf") format("truetype"),
  url("../fonts/Mark-Simonson/regular/ProximaNova-Regular.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'custom-semibold';
  src: url("../fonts/Mark-Simonson/semibold/ProximaNova-Semibold.eot") format("eot"),
  url("../fonts/Mark-Simonson/semibold/ProximaNova-Semibold.woff") format("woff"),
  url("../fonts/Mark-Simonson/semibold/ProximaNova-Semibold.ttf") format("truetype"),
  url("../fonts/Mark-Simonson/semibold/ProximaNova-Semibold.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'custom-bold';
  src: url("../fonts/Mark-Simonson/bold/ProximaNova-Bold.eot") format("eot"),
  url("../fonts/Mark-Simonson/bold/ProximaNova-Bold.woff") format("woff"),
  url("../fonts/Mark-Simonson/bold/ProximaNova-Bold.ttf") format("truetype"),
  url("../fonts/Mark-Simonson/bold/ProximaNova-Bold.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'custom-black';
  src: url("../fonts/Mark-Simonson/black/ProximaNova-Black.eot") format("eot"),
  url("../fonts/Mark-Simonson/black/ProximaNova-Black.woff") format("woff"),
  url("../fonts/Mark-Simonson/black/ProximaNova-Black.ttf") format("truetype"),
  url("../fonts/Mark-Simonson/black/ProximaNova-Black.svg") format("svg");
  font-weight: normal;
  font-style: normal;
}

p, span,label {
  font-family: custom-regular;
}
li{
  font-family: custom-regular;

}
button, input, a {
  cursor: pointer;
  outline: none;
  -webkit-tap-highlight-color: transparent;
}

a {
  -webkit-tap-highlight-color: transparent;
  text-decoration: none;
  font-family: custom-regular;

}

ul {
  list-style: none;
}

a, button {
  transition: .3s;
  &:hover, &:focus {
    transition: .3s;

  }
}

section {
  margin: auto;
}

//common block start
//forms start
label.checkbox{
  cursor: pointer;
  font-size: 19px;
  position: relative;
  padding-left: 25px;
  margin-bottom: 10px;
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
  span{
    font-size: 19px;
  }
  a{
    color: black;
  }
}
.contacts__form{
  margin-top: 40px;
  button{
    margin:0 auto 50px;
  }

  .form-row {
    flex-direction: column;
    display: flex;
    width: 100%;
    text-align: left;
    margin-bottom: 30px;
    &:last-child{
      margin-bottom: 0;
    }
    &.error{
      input{
        border-color: $red;
      }
      .cback-inp-error-text{
        display: block;
      }
    }
  }
  .label {
    margin-bottom: 10px;
    color: #424343;
    font-size: 21px;
    font-weight: 400;
    line-height: 22px;
  }
  textarea{
    width: 100%;
    outline: none;
    font-size: 17px;
    font-family: custom-regular;
    padding:5px 15px;
    box-sizing: border-box;
    height: 100%;
    background-color: white;
    border: 1px solid $gray;
  }
  input{
    padding:5px 15px;
    width: 100%;
    font-size: 17px;
    height: 41px;
    background-color: white;
    border: 1px solid $gray;
    box-sizing: border-box;
  }
  .cback-inp-error-text {
    color: red;
    font-size: 11px;
    margin-bottom: -13px;
    display: none;
  }
  .form__in{
    margin-bottom: 50px;
    display: flex;
    justify-content: space-between;
    .f__right{
      width: 56%;
      .form-row{
        height: 100%;
      }
    }
    .f__left{
      width: calc(41% - 20px);
      display: flex;
      flex-direction: column;
    }
  }
  .form_part{
    width: calc(50% - 20px);
    display: flex;
    flex-direction: column;
  }
  .form_col{
    flex-direction: row;
    justify-content: space-between;
  }
}
@media screen and (max-width: 1280px){
  .contacts__form .label{
    font-size: 16px;
    margin-bottom: 3px;
    line-height: 19px;
  }
}
@media screen and (max-width: 720px) {
  .register_form .form__in{
    flex-direction: column;
  }
  .register_form .form_part{
    margin-bottom: 30px;
    width: 100%;
  }
  .register_form .form_col{
    flex-direction: column;
  }
}
@media screen and (max-width: 576px) {
  .contacts__form .label{
    font-size: 17px;
  }
  .contacts__form .form__in .f__left{
    width: 100%;
  }
  .contacts__form .form__in .f__right{
    margin-top: 30px;
    width: 100%;
  }
  .contacts__form .form__in{
    flex-direction: column;
  }
  .contacts__form textarea{
    min-height: 200px;

  }
}
//forms end

.annotation{
  p{
    font-size: 16px;
  }
}
@media screen and (max-width: 576px){
  .annotation{
    p{
      font-size: 13px;
    }
  }
}
//poopup start
.popup_title{
  font-size: 25px;
  font-family: custom-bold;
  margin-bottom: 10px;
  span{
    font-family: custom-bold;
  }
}
.default_popup{
  box-sizing: border-box;
  opacity: 0;
  visibility: hidden;
  background: white;
  border-radius: 15px;
  padding: 25px;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) scale(0,0);
  &.active{
    z-index: 20;
    opacity: 1;
    visibility: visible;
    transform: translate(-50%, -50%) scale(1,1);

  }
  .popup_close{
    position: absolute;
    top: 10px;
    right: 10px;
  }
}
//poopup end

.hide{
  display: none;
}

//counter start
.counter {
  border: 1px solid #aeaeae;
  display: flex;
  width: 80px;
  box-sizing: border-box;
  height: 32px;
}
.counter button {
  cursor: pointer;
  color: $gray;
  width: 25px;
  height: 30px;
  box-sizing: border-box;
  background: transparent;
  border: none;
  padding: 0;
  transition: .3s;

  &:hover, &:focus {
    color: black;
    transition: .3s;
    background: $hoverBtn;
  }
}
.counter input {
  font-size: 21px;
  box-sizing: border-box;
  width: 29px;
  border: none;
  font-family: custom-bold;
  outline: none;
  height: 29px;
  text-align: center;
}
//counter end

//video start

.video {
  position: relative;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
  background-color: #000000;
}

.video__link {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.video__media {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

.video__button {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  display: none;
  padding: 0;
  width: 68px;
  height: 48px;
  border: none;
  background-color: transparent;
  transform: translate(-50%, -50%);
  cursor: pointer;
}

.video__button-shape {
  fill: #212121;
  fill-opacity: 0.8;
}

.video__button-icon {
  fill: #ffffff;
}

.video__button:focus {
  outline: none;
}

.video:hover .video__button-shape,
.video__button:focus .video__button-shape {
  fill: #ff0000;
  fill-opacity: 1;
}

/* Enabled */

.video--enabled {
  cursor: pointer;
}

.video--enabled .video__button {
  display: block;
}

//video end



