//switch btn
.switch {
  -webkit-tap-highlight-color: transparent;

  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
}
.switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: $grayDark;
  -webkit-transition: .4s;
  transition: .4s;
}
.switch .slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 1px;
  bottom: 1px;
  background-color:#b9bbba;
  -webkit-transition: .4s;
  transition: .4s;
}
.switch input:hover + .slider,
.switch input:focus + .slider {
  box-shadow: 0px 0px 13px -2px black;

}
.switch input:checked + .slider {
  background-color: $red;
}
.switch input:focus + .slider {
}
.switch input:checked + .slider:before {
  transform: translateX(20px);
  background: white;
}
.switch .slider.round {
  border-radius: 34px;
  height: 20px;
  width: 40px;
}
.switch .slider.round:before {
  border-radius: 50%;
}
//switch btn end

