//sort catalog page start
.sort__method {
  position: relative;
  font-size: 18px;
  display: flex;
  align-items: flex-end;
  margin-bottom: -5px;
  padding-right: 20px;
  padding-left: 15px;
  .method__list {
    a {
      white-space: nowrap;
      padding: 2px 5px;
      font-size: 16px;
      color: #babcbb;
      &:hover, &:focus {
        background: #aeaeae;
        color: black;
      }
    }
    border: 1px solid #c7c7c7;
    z-index: 3;
    box-shadow: 10px 10px 35px -12px rgba(0, 0, 0, 0.75);
    box-sizing: border-box;
    top: 45px;
    right: 4px;
    background: white;
    padding: 5px;
    position: absolute;
    visibility: hidden;
    display: flex;
    flex-direction: column;
    transition: .2s;
    max-height: 0px;
    overflow: hidden;
    &.active {
      transition: .2s;
      max-height: 180px;
      visibility: visible;
      overflow: unset;
    }
  }
  .triangle-left {
    position: absolute;
    top: -10px;
    right: -1px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 0 10px 7.5px;
    border-color: transparent transparent #c7c7c7 transparent;
  }
  .triangle-left:after {
    position: absolute;
    content: "";
    display: block;
    top: 3px;
    z-index: 3;
    left: -6.5px;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 0 8px 5.5px;
    border-color: transparent transparent white transparent;
    border-color: transparent transparent white transparent;
  }
  .selected__method {
    cursor: pointer;
    &:hover, &:focus{
      span {
        text-decoration: underline;
      }
    }
  }
  span {
    white-space: nowrap;
    position: relative;
    &:after {
      position: absolute;
      top: 9px;
      right: -19px;
      width: 10px;
      height: 11px;
      content: "";
      background: url(../images/icons/triangle.png) no-repeat;
      background-size: contain;
    }
  }
}
@media screen and (max-width: 576px){
  .sort__method .method__list{
    transform: scale(0);
    transition: .1s;
  }
  .sort__method .method__list.active{
    max-height: unset;
    overflow: unset;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: white;
    z-index: 3;
    transform: scale(1);
    transition: .3s;
  }
  .sort__method{
    position: absolute;
    width: 1px;
    height: 1px;
    overflow: hidden;
    clip: rect(0 0 0 0);
    &.active{
      clip:unset;
      overflow: unset;
      .selected__method{
        opacity: 0;
      }
    }
  }
  .close__sorting{
    justify-content: space-between;
    padding-right: 10px;
    box-sizing: border-box;
    padding-top: 10px;
    margin-bottom: 35px;
    padding-bottom: 10px;
    border-bottom: 1px solid black;
  }
  .sort__method .method__list a {
    padding: 2px 5px;
    margin-bottom: 15px;
    font-size: 17px;
    color: black;
  }
  .sorting__title{
    font-size: 25px;
  }
}
//sort catalog page end

