.icon-close {
  width: 27px;
  height: 27px;

  span:before, span:after {
    top: 11px;
    left: 6px;
    width: 13px;
  }
  span:before {
    transform: rotate(45deg);
  }
  span:after {
    transform: rotate(-45deg);
  }
  span,
  span:before,
  span:after {
    cursor: pointer;
    border-radius: 1px;
    position: absolute;
    display: block;
    content: '';
    transition: all 300ms ease-in-out;
    width: 13px;
    background: $red;
    height: 3px;
  }
  span {
    background: transparent;
    width: 25px;
    height: 25px;
    -webkit-tap-highlight-color: transparent;
    border-radius: 50%;
    border: 1px solid #aeaeae;
  }
  &:hover,&:focus{
    span {
      box-shadow: 0px 0px 13px -2px black;
    }
  }
}

.slick-prev {
  z-index: 2;
  left: 10px !important;
  width: 45px !important;
  height: 45px !important;
}

.slick-dots li button {
  padding: 0 !important;
  width: 16px !important;
  height: 16px !important;
}

.slick-dots li {
  height: 16px !important;
  width: 16px !important;
}

.slick-dots li button:before {
  content: "" !important;
  width: 14px !important;
  opacity: 1 !important;
  height: 14px !important;
  background: $grayDark;
  border-radius: 50%;
  font-size: 18px !important;
}

.slick-dots li.slick-active button:before {
  background: white;
  border: 1px solid $grayDark;

}

.slick-next {
  width: 45px !important;
  height: 45px !important;
  z-index: 2;
  right: 10px !important;
}

.slick-next:before {
  width: 45px;
  height: 45px;
  display: block;
  background: url("../images/icons/arrow_right_red.svg") no-repeat;
  background-size: contain;
  content: "" !important;
  font-family: custom-regular !important;
}

.slick-prev:before {
  width: 45px;
  height: 45px;
  display: block;
  font-family: custom-regular !important;
  content: "" !important;
  background: url("../images/icons/arrow_right_red.svg") no-repeat;
  background-size: contain;
  transform: rotateZ(180deg);
}

.white-arrows {
  .slick-prev {
    left: 10px !important;
  }
  .slick-next {
    right: 10px !important;
  }
  .slick-next:before {
    background: url("../images/icons/arrow_right.svg") no-repeat;
    background-size: contain;
  }
  .slick-prev:before {
    background: url("../images/icons/arrow_right.svg") no-repeat;
    background-size: contain;
    transform: rotateZ(180deg);
  }
}

@media screen and (max-width: 576px) {
  .white-arrows {
    .slick-prev {
      width: 24px !important;
      height: 7px !important;
    }
    .slick-next {
      width: 24px !important;
      height: 7px !important;
    }
    .slick-next, .slick-prev {
      bottom: -17px;
      transform: unset;
      top: unset;
    }
    .slick-prev:before {
      width: 24px !important;
      height: 7px !important;
      background: url("../images/icons/arow_r_icon.svg") no-repeat;
      background-size: contain;
    }
    .slick-next:before {
      width: 24px !important;
      height: 7px !important;
      background: url("../images/icons/arow_r_icon.svg") no-repeat;
      background-size: contain;
    }
  }
  .slick-prev {
    width: 24px !important;
    height: 7px !important;
  }
  .slick-next {
    width: 24px !important;
    height: 7px !important;
  }
  .slick-next, .slick-prev {
    bottom: -17px;
    transform: unset;
    top: unset;
  }
  .slick-prev:before {
    width: 24px !important;
    height: 7px !important;
    background: url("../images/icons/arow_r_icon.svg") no-repeat;
    background-size: contain;
  }
  .slick-next:before {
    width: 24px !important;
    height: 7px !important;
    background: url("../images/icons/arow_r_icon.svg") no-repeat;
    background-size: contain;
  }
}

.custom-button {
  border: none;
  text-transform: uppercase;
  font-size: 19px;
