        text-align: center;
        border-radius: 50%;
        box-sizing: border-box;
        border: 1px solid transparent;
        transition: all .2s ease-in-out;
        &.state-active {
          border-color: $gray;
        }
        &:hover, &:focus {
          border-color: $gray;
        }
        span {
          position: relative;
          border-radius: 50%;
