          line-height: 16px;
          display: block;
          width: 14px;
          height: 14px;
          border-radius: 50%;
          position: relative;
          &:before {
            position: absolute;
            left: 50%;
            top: 50%;
            -webkit-transform: translate(-50%, -50%);
            -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
            z-index: 1;
            display: inline-block;
            width: 16px;
            height: 16px;
            border: 1px solid rgba(34, 31, 31, .1);
            border-radius: 50%;
