.cart_items_title{
  display: none;
}
  .cart_itm_p1{
    width: calc(100% - 90px);
    flex-direction: column;
  }
  .cart_item .c-title{
    display: block;
  }
  .cart_item {
    .cart_itm_kinds {
      .colors {
        display: none;
      }
      .weights {
        display: none;
      }
