      .cart_itm_kinds {
        .colors {
          display: none;
        }
        .weights {
          width: 80px;
          text-align: center;
          .empty {
            display: block;
          }
          .weight-name{
            display: none;
          }
          .counter {
            display: none;
          }
        }
      }
    }
    &.pr__weight {
      .cart_itm_kinds {
        .weights {
          display: block;

          .empty {
            display: none;
          }
          .weight-name{
            display: block;
          }
          .counter {
            display: flex;
          }
        }
      }
    }
    &.pr__colors {

      .cart_itm_kinds {
        .colors {
          display: block;

          .empty {
            display: none;
          }
          .color-name {
            display: block;
          }
        }
      }
    }
  }

