//order popup start
.order_popup{
  max-width: 1290px;
  width: 100%;
  .popup_title{
    margin-bottom: 40px;
  }
  .cart_bottom{
    margin: 30px 0 0 0;
  }
  .custom-button{
    font-size: 21px;
  }
}
.order_table{
  border: 1px solid $gray;
}
.order_row{
  min-height: 40px;
  align-items: stretch;
  display: flex;
  border-bottom: 1px solid $gray;
  &:last-child{
    border: none;
  }
}
.order_col{
  padding: 10px 5px;
  box-sizing: border-box;

  //height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  p{
    font-size: 19px;
  }
}
.order_name{
  justify-content: flex-start;
  width: 38%;
  p{
    font-size: 21px;
  }
}
.order_c{
  width: 15.5%;
  text-align: center;
  span.product_color{
    position: relative;
    border-radius: 50%;
    font-size: 10px;
    line-height: 16px;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    &:after{
      position: absolute;
      left: 50%;
      top: 50%;
      -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
      z-index: 1;
      display: inline-block;
      width: 22px;
      height: 22px;
      border: 1px solid $gray;
      border-radius: 50%;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      content: "";
    }
  }
}
.order_w{
  text-align: center;
  border-left: 1px solid $gray;
  border-right: 1px solid $gray;
  width: 15.5%;
  p{
    font-size: 15px;
  }
}
.order_count{
  border-left: 1px solid $gray;
  border-right: 1px solid $gray;
  p{
    font-family: custom-bold;
  }
  text-align: center;

  width: 15.5%;
}
.order_price{
  text-align: center;
  p,span{
    font-family: custom-bold;
  }
  width: 15.5%;
}
.order_row{
  .empty {
    color: $gray;
  }
  .order_w{
    p{
      display: none;
    }
  }
  .order_c{
    span.product_color{
      display: none;
    }
  }
  &.pr__weight{
    .order_w{
      p{
        display: block;
      }
      .empty{
        display: none;
      }
    }
  }
  &.pr__colors{
    .order_c{
      span.product_color{
        display: block;
      }
      .empty{
        display: none;
      }
    }
  }
}
@media screen and (max-width: 992px){
  .order_popup .cart_bottom_left .cart_total .total_count{
    margin-left: 5px;
  }
  .order_popup .custom-button {
    font-size: 17px;
    padding: 10px 8px;
  }
  .order_popup .cart_bottom_left .cart_total p,.order_popup .cart_bottom_left .cart_total span {
    font-family: custom-bold;
    font-size: 21px;
  }
}
@media screen and (max-width: 576px){
  .order_col{
    padding: 5px 5px;

    p{
      font-size: 15px;
    }
  }

  .order_c{
    width: 13.5%;
    p{
      font-size: 12px;
    }
  }
  .order_w{
    p{
      font-size: 12px;
    }
    width: 13.5%;
  }
  .order_count{
    width: 13.5%;
  }
  .order_price{
    width: 21.5%;
  }
  .order_popup .popup_title {
    margin-bottom: 10px;
    font-size: 17px;
  }
  .order_row {
    min-height: 33px;
  }
  .order_popup .custom-button{
    min-width: unset;
    font-size: 17px;
  }
  .order_popup .cart_bottom_left .cart_total p, .order_popup .cart_bottom_left .cart_total span {
  }
}

@media screen and (max-width: 390px){
  .order_col p {
    font-size: 13px;
  }
}
